import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        brands: []
    },
    mutations: {
        setBrands (state, brands) {
            state.brands = brands;
        }
    },
    actions: {
        async getBrands ({
            commit
        }) {
            try {
                let {
                    data
                } = await Vue.prototype.$http.post('/mobile/car/brandlist', {
                    p: 1
                });

                commit('setBrands', data);
            } catch (error) {
                console.log(error);
            }
        }
    }
});



